export default "随着数据量的日益增长，传统数据库已无法满足业务对性能，扩展性，高可用性的需求;业界也不断推出新的数据库解决方案，如基于共享存储的ORACLE RAC集群，中间件分库分表等;但这些解 决方案都存在问题，无法满足业务需求。\n\nOBASE 是一个新型的分布式关系型数据库系统，支持多活部署，不仅具有良好的扩展性、高可 用性、支持关系模型、支持事务处理、采用标准 SQL 接口，同时不需要用户进行分库分表，没有跨节点操作限制，因此更适合于大规模结构化数据管理和高并发事务处理的应用场景。\n\n## 原理及架构\n\nOBASE集群中存在4种角色的服务进程，其中CG进程实现了传统数据库的查询解析、编译和执 行的功能;DG进程实现了数据文件的分布式存储与访问功能;TG进程基于内存引擎实现了高吞吐量的 事务处理功能;MG实现了集群管理功能。集群中通常部署多个DG和CG节点，通过添加节点数量可以 扩展系统的存储和查询处理能力。集群中可以部署多个TG节点，采用基于Quorum的提交协议，能够 保证在多数派节点正常的情况下不丢失数据。集群中同样可以部署多个MG节点，通过Paxos协议来实 现自动选主，保证了集群的高可用。\n\n## ManEng:简称MG，系统的控制节点，负责集群管理和数据分布\n\n集群中可以设置多个MG节点(一主多备)，通过Paxos协议实现自动选主，能够保证集群的高可用;\n通过租约机制(Lease)来管理集群中的TG节点，保证同一时刻只有一个TG提供事务处理服务;\n通过心跳机制(Heartbeat)来管理集群中的CG和DG节点，感知节点的上下线;\nOBASE中的数据表按主键范围被划分为多个子表(称为Tablet)，每个子表可以有多个副本，分别存储在不同的DG节点上。MG通过RootTable结构来记录Tablet在DG上的分布位置信息，并管理Tablet的副本数量、复制、迁移和合并。"