export default "## 1. 规划\n### 1.1 软硬件要求\n\n虽然使用一台物理机就可以安装和运行OBASE系统，但这样的环境多用于学习和测试。为了充分发挥分布式系统的优势，建议使用3台以上的物理机环境。由于集群中不同类型的节点所消耗的主要系统资源不同，在规划OBASE数据库集群时，通常将MG和TG安装在同一台物理机上，将DG和CG安装在同一台物理机上。\n安装OBASE数据库系统所需的软硬件环境如表1-1所示。\n\n<div class=\"img-descr\">表1-1：OBASE数据库系统软硬件配置需求</div>\n<img src=\"../images/env-table1.png\">\n\n注：也可使用具有相同内核的CentOS操作系统，尚不支持Red Hat 7操作系统；\n\n### 1.2 部署模式\n\nOBASE分布式数据库系统支持多种部署方式，见表2-2。用户可以根据需要选择合适的部署方式，并且能够在使用过程中进行灵活的调整。\n<div class=\"img-descr\">表1-2：OBASE数据库系统的部署方式</div>\n<img src=\"../images/env-table2.png\">\n\n### 1.3 安装规划\n在安装OBASE数据库之前，数据库管理员首先应当对系统的资源和环境进行规划，包括服务器的使用，IP地址和端口、安装目录、数据目录和磁盘挂载点等内容。\n\n### 1.4 服务器规划\n数据库管理员可以根据示例表1-3对OBASE系统所使用的服务器进行规划，包括服务器所属集群，IP地址、网卡名称、安装用户、安装目录、所运行服务、使用端口等信息。\n\n<div class=\"img-descr\">表1-3：OBASE数据库系统服务器使用规划示例</div>\n<img src=\"../images/env-table3.png\">\n\n注：这一个单机房(单MG/TG)的部署，使用了4台服务器；\n\n### 1.5 目录规划\n数据库管理员需要规划和管理每台服务器上不同服务存储数据的目录位置，并且需要根据数据规模、使用频率、访问性能等需求将数据目录与相应的磁盘挂载点关联起来。OBASE系统中的服务需要持久化到磁盘上的内容包括日志和数据，表1-4是一个目录规划的示例。\n\n<div class=\"img-descr\">表1-4：OBASE数据库服务目录使用规划示例</div>\n<img src=\"../images/env-table4.png\">\n\n### 1.6 磁盘挂载点规划\nOBASE系统中的TG服务和DG服务分别需要存储大规模的增量数据和静态数据，建议不与操作系统和其它服务共享磁盘，而使用单独的磁盘来存储这些服务的数据。数据库管理员需要对每台服务器上的磁盘挂载点进行规划，并且能够与目录规划相对应。\n例如，在一台运行了DG的服务器上，规划8块磁盘用于专门存储DG的数据，数据库管理员可以在OBASE的安装目录下创建DG的数据目录，如下：\n```\n/home/admin/obase/data/1\n/home/admin/obase/data/2\n...\n/home/admin/obase/data/8\n```\n\n然后将8块磁盘的挂载点设为：\n```\n/data/1\n/data/2\n...\n/data/8\n```\n\n最后在挂载点目录与数据目录之间建立链接：\n```\nln -s  /data/1  /home/admin/obase/data/1\nln -s  /data/2  /home/admin/obase/data/2\n...\nln -s  /data/8  /home/admin/obase/data/8\n```\n\n## 2. 配置安装环境\n\n为了能够正常运行OBASE数据库系统，数据库管理员需要对安装环境进行必要的配置，包括设置网卡、内核参数、时间同步、环境变量等。\n\n### 2.1 操作系统配置\n\n#### 2.1.1 配置网卡名称\n\n在启动OBASE系统服务时，需要在启动命令中指定所使用的网卡名称。为了便于记忆和管理，建议在所有的服务器上使用相同的网卡名称，例如“eth0“。如果在不同服务器上使用了不同的网卡名称，在启动服务时需要特别注意指定网卡名称的参数是否正确。\n设置服务器网卡名称为“eth0“的操作步骤如下：\n1. 以root用户登录OBASE服务器；\n2. 编辑修改“/etc/udev/rules.d/70-persistent-net.rules”文件的内容；\n<img src=\"../images/env-pic1.png\">\n3. 编辑修改“/etc/sysconfig/network-scripts/ifcfg-eth0”文件的内容；\n<img src=\"../images/env-pic2.png\">\n注：IPADDR需要设置为实际使用的IP地址\n4. 通过执行reboot命令重新启动服务器；\n5. 通过执行ifconfig命令，确认网卡名称设置正确；\n<img src=\"../images/env-pic3.png\">\n\n#### 2.1.2 配置内核参数\n\n配置操作系统内核参数的操作步骤如下：\n1. 以root用户登录OBASE服务器；\n2. 编辑“/etc/sysctl.conf”文件，在末尾添加以下内容：\n    ```\n    # for OBASE\n    net.core.somaxconn = 2048\n    net.core.netdev_max_backlog = 10000\n    net.core.rmem_default = 16777216\n    net.core.wmem_default = 16777216\n    net.core.rmem_max = 16777216\n    net.core.wmem_max = 16777216\n    net.ipv4.ip_local_port_range = 3500 65535\n    net.ipv4.ip_forward = 0\n    net.ipv4.conf.default.rp_filter = 1\n    net.ipv4.conf.default.accept_source_route = 0\n    net.ipv4.tcp_syncookies = 0\n    net.ipv4.tcp_rmem = 4096 87380 16777216\n    net.ipv4.tcp_wmem = 4096 65536 16777216\n    net.ipv4.tcp_max_syn_backlog = 16384\n    net.ipv4.tcp_fin_timeout = 15\n    net.ipv4.tcp_max_syn_backlog = 16384\n    net.ipv4.tcp_tw_reuse = 1\n    net.ipv4.tcp_tw_recycle = 1\n    vm.swappiness = 0\n    ```\n3. 执行以下命令，使修改生效；\n    ```\n    /sbin/sysctl -p /etc/sysctl.conf\n    ```\n\n#### 2.1.3 配置时间同步\n\nOBASE数据库系统通常运行于PC服务器集群之上，对于集群中服务器间的时钟误差有较为严格的要求，可以通过NTP时钟同步来保证所有的服务器时间一致。在系统运行期间，如果发生服务器重启的情况，需要关注NTP时钟同步服务是否正常。\n\n使用NTP时钟同步需要关闭所有服务器的防火墙(或者开放NTP服务相应端口)，关闭防火墙的命令为：\n```\nservice iptables stopl\n```\n\n> 开启NTP服务\n\n如果没有现成可用的NTP服务器，可以在OBASE数据库集群中任选一台服务器作为NTP服务器，在其上开启NTP Server服务，操作步骤如下：\n1. 以root用户登录NTP Server服务器；\n2. 编辑修改“/etc/ntp.conf”文件内容；\n<img src=\"../images/env-pic4.png\">\n3. 执行以下命令，重启NTP Server服务；\n    ```\n    /etc/init.d/ntpd restart\n    ```\n4. 执行以下命令，查看NTP Server服务是否启动；\n    ```\n    ps -ef |grep -i ntpd\n    ```\n5. 执行以下命令，设置NTP服务开机自动启动；\n    ```\n    chkconfig ntpd onl\n    ```\n\n> 开启NTP客户端\n\n在启动NTP Server服务之后，需要等待5分钟，才能在其它服务器上开启NTP Client服务，否则会提示“no server suitable for synchronization found”错误。假设NTP Server服务器的IP地址为192.168.1.4，在所有其它服务器上开启NTP Client服务的操作步骤如下：\n1. 以root用户登录服务器；\n2. 执行以下命令，停止NTP服务；\n    ```\n    /etc/init.d/ntpd stop\n    ```\n3. 执行以下命令，同步NTP Server的时间；\n    ```\n    ntpdate 192.168.1.4\n    ```\n4. 执行crontab -e命令，并添加以下内容，设置定时任务每10分钟同步一次时间；\n    ```\n    0-59/10 * * * * /usr/sbin/ntpdate 192.168.1.4&&/sbin/hwclock –w\n    ```\n\n#### 2.1.4 配置资源限制\nLinux操作系统默认所使用堆栈和文件数的限制值较小，为了正常运行OBASE数据库服务，需要放大对资源的限制，操作步骤如下：\n1. 以root用户登录服务器；\n2. 编辑修改配置文件”/etc/security/limits.conf”，添加如下信息：\n    ```\n    * soft stack 204800\n    * hard stack 204800\n    * soft nofile 65535\n    * hard nofile 65535\n    ```\n\n### 2.2 创建安装用户\n\n在所有OBASE服务器中创建安装用户的操作步骤如下：\n1. 以root用户登录服务器；\n2. 执行如下命令，创建安装用户admin；\n    ```\n    useradd -d /home/admin -s /bin/bash -m admin\n    ```\n3. 执行如下命令，为用户admin设置密码；\n    ```\n    passwd admin\n    ```\n4. 为用户admin赋予sudo权限；\n    - 执行以下命令，添加“/etc/sudoers”文件的写权限\n    ```\n    chmod u+w /etc/sudoers\n    ```\n    - 编辑“/etc/sudoers”文件中“root ALL=(ALL) ALL”后添加如下黑体语句：\n    ```\n    root ALL=(ALL) ALLadmin ALL=(ALL) ALL\n    ```\n    - 执行以下命令，删除“/etc/sudoers”文件的写权限；\n    ```\n    chmod u-w /etc/sudoers\n    ```\n\n### 2.3 配置环境变量\n为了正常运行OBASE数据库系统，需要对安装用户admin的环境变量进行配置，操作步骤如下：\n1. 以admin用户登录OBASE服务器；\n2. 编辑修改“/home/admin/.bashrc”文件，添加如下语句：\n    ```\n    export LD_LIBRARY_PATH=/home/admin/obase/lib:/usr/local/lib64:/usr/:/usr/lib/:/usr/local/lib:/lib\n    export PATH=~/obase/bin:$PATH\n    ulimit -c unlimited\n    ```\n3. 执行以下命令，让环境配置生效；\n    ```\n    source ~/.bashrc\n    ```\n\n### 2.4 配置服务器间免登录\n\n在所有的OBASE服务器中选择一台作为本机，配置该服务器到所有服务器的免登录（包括本机到本机）。配置免登录后，该服务器在连接其他服务器时无需输入密码。\n\n#### 2.4.1 配置过程\n\n假设本机的IP地址为“192.168.1.4”，配置免登录的操作步骤如下：\n1. 以admin用户登录服务器；\n2. 执行以下命令，并按“Enter”键，直至生成公钥；\n    ```\n    ssh-keygen -t rsa -P ''\n    ```\n3. 执行以下命令，并根据提示输入登录密码，配置免登录；\n    ```\n    ssh-copy-id admin@192.168.1.2\n    ssh-copy-id admin@192.168.1.3\n    ssh-copy-id admin@192.168.1.4\n    ssh-copy-id admin@192.168.1.5\n    ```\n\n#### 2.4.2 验证\n \n配置免登录完成后，在本机中输入“ssh admin@192.168.1.X”；\n- 如果无需输入密码，则表示配置免登录成功；\n- 如果仍需要输入密码，则请重新配置免登录；\n\n## 3.  RPM包安装\n\n### 3.1 复制安装包\n\n将OBASE数据库的安装文件复制到”~/install”目录下；\n\n### 3.2 安装依赖库\n1. 以admin用户登录服务器；\n2. 执行以下命令，进入安装目录；\n    ```\n    cd ~/install\n    ```\n3. 执行以下命令，安装依赖库；\n    ```\n    sudo rpm –ivh mysql-5.1.73-7.el6.x86_64.rpm（如已有mysql客户端则不必安装）sudo rpm -ivh snappy-1.1.2-1.x86_64.rpmsudo rpm -ivh lzo-2.06-0.x86_64.rpm\n    ```\n\n### 3.3 安装OBASE数据库\n\n执行一下命令，安装OBASE数据库：\n```\nsudo rpm –ivh obase-1.0.44.x86_64.rpm --prefix=/home/admin/obase\n```\n注：如果不加prefix参数，默认安装在/home/admin/obase目录下；\n\n### 3.4 创建数据目录\n\n本样例安装挂载使用了4块数据盘到/data/1..4，数据目录：~/obase/data最终结构如下\n```tree\ndata\n├── 1 -&gt; /data/1/admindata/dg_data\n├── 2 -&gt; /data/2/admindata/dg_data\n├── 3 -&gt; /data/3/admindata/dg_data\n├── 4 -&gt; /data/4/admindata/dg_data\n├── mg\n├── mg_commitlog -&gt; /data/1/admindata/commitlog/mg_commitlog\n├── tg_commitlog -&gt; /data/1/admindata/commitlog/tg_commitlog\n└── tg_data\n    ├── raid0\n    │   ├── store0 -&gt; /data/1/admindata/tg_data/1\n    │   └── store1 -&gt; /data/2/admindata/tg_data/2\n    └── raid1\n        ├── store0 -&gt; /data/3/admindata/tg_data/3\n        └── store1 -&gt; /data/4/admindata/tg_data/4\n```\n\n1. 以admin用户登录服务器，并进入安装目录~/obase；\n2. 执行以下命令，创建和配置所需数据目录:\n    ```\n    sh RebuildDir.sh -m performence -u 4 -c 4 –d obhdfs\n    ```\n\n参数含义：\n-m: 创建数据目录的模式：[test|performence] (performance使用/data/1..n作为数据挂载点，test使用~/data作为数据挂载点，默认test)\n-u: MG/TG服务所使用的挂载磁盘数目(默认 4)\n-c: DG/CG 服务所使用的挂载磁盘数目(默认 4)\n-d: 应用名(默认为obtest)\n注：sh RebuildDir.sh –h 查看更多参数选择；\n\n\n## 4 启动与管理系统\n\n在每台服务器上正确安装OBASE软件并且创建数据目录之后，就可以启动数据库服务。\n\n### 4.1数据库服务启动命令\n\n进入obase安装目录：`cd ~/obase`\n\n#### 4.1.1 MG启动命令\n主MG启动命令格式：\n```\nbin/manEng -r 192.168.1.2:2500 -R 192.168.1.2:2500 -i eth0 -U 3 -u 3 -C 1\n```\n备MG启动命令格式：\n```\nbin/manEng -r 192.168.1.3:2500 -R 192.168.1.2:2500 -i eth0 -C 1\n```\n\n参数含义：\n-r：所启动MG服务的IP地址和端口；\n-R：系统主MG服务的IP地址和端口；\n-i：绑定的网卡名；-C：系统中所属逻辑集群ID，必须为数字；\n-U：指定系统中MG服务的数量；\n-u：指定系统中TG服务的数量；\n\n#### 4.1.2 TG启动命令\n命令格式：\n```\nbin/transEng -r 192.168.1.2:2500 -p 2700-m 2701 -i eth0 -C 1\n```\n\n参数含义：\n-r：系统主MG服务的IP地址和端口；\n-p：所启动TG服务的端口号；\n-m：所启动TG服务的内部端口号；\n-i：绑定的网卡名；\n-C：系统中所属逻辑集群ID，必须为数字；\n\n#### 4.1.3 DG启动命令\n命令格式：\n```\nbin/datEng -r 192.168.1.2:2500 -p 2600 -n obhdfs -i eth0 -C 1\n```\n\n参数含义：\n-r：系统主MG服务的IP地址和端口；\n-p：所启动DG服务的端口号；\n-n：指定应用名，应当与建DG数据目录时所设定的应用名一致；\n-i：绑定的网卡名；\n-C：系统中所属逻辑集群ID，必须为数字；\n\n#### 4.1.4 CG启动命令\n\nCG启动命令格式：\n```\ncalEng -r 192.168.1.2:2500 -p 2800 -z 2880 -i eth0 -C 1\n```\n\n参数含义：\n-r：系统主MG服务的IP地址和端口；\n-p：所启动DG服务的端口号；\n-z：所启动CG的MySQL协议服务端口号；\n-i：绑定的网卡名；\n-C：系统中所属逻辑集群ID，必须为数字；\n\n### 4.2 单集群启动示例\n\n以一个逻辑集群组成的OBASE数据库系统为例，集群号为1，使用三台服务器，在每台服务器上都启动MG、TG、CG和DG服务，其中主MG所在的服务器上启动CG被设置为。指定主MG为192.168.1.4:2500。\n\n以admin用户登录所有服务器，在安装目录/home/admin/obase下执行启动命令。\n\nServer 1（主MG所在服务器）\n```\nbin/manEng -r 192.168.1.4:2500 -R 192.168.1.4:2500 -i eth0 -U 3 -u 3 -C 1\nbin/transEng -r 192.168.1.4:2500 -p 2700 -m 2701 -i eth0 -C 1\nbin/datEng -r 192.168.1.4:2500 -p 2600 -n obhdfs -i eth0 -C 1\nbin/calEng -r 192.168.1.4:2500 -p 2800 -z 2880 -i eth0 -C 1\n```\nServer 2\n```\nbin/manEng -r 192.168.1.3:2500 -R 192.168.1.4:2500 -i eth0 -C 1\nbin/transEng -r 192.168.1.4:2500 -p 2700 -m 2701 -i eth0 -C 1\nbin/datEng -r 192.168.1.4:2500 -p 2600 -n obhdfs -i eth0 -C 1\nbin/calEng -r 192.168.1.4:2500 -p 2800 -z 2880 -i eth0 -C 1\n```\nServer 3\n```\nbin/manEng -r 192.168.1.7:2500 -R 192.168.1.4:2500 -i eth0 -C 1\nbin/transEng -r 192.168.1.4:2500 -p 2700 -m 2701 -i eth0 -C 1\nbin/datEng -r 192.168.1.4:2500 -p 2600 -n obhdfs -i eth0 -C 1\nbin/calEng -r 192.168.1.4:2500 -p 2800 -z 2880 -i eth0 -C 1\n```\n\n如果有其它服务器加入到集群中，可以在这些服务器上启动DG和CG服务，命令如下：\n```\nbin/datEng -r 192.168.1.4:2500 -p 2600 -n obhdfs -i eth0 -C 1\nbin/calEng -r 192.168.1.4:2500 -p 2800 -z 2880 -i eth0 -C 1\n```\n注：MG和TG已经达到设定数目；\n\n在所有服务器上执行完毕命令后，在确认所有服务进程都已启动，在任意一台服务的安装目录下执行以下命令进行数据库系统的初始化：\n```\nbin/mg_admin -r 192.168.1.4 -p 2500 -t 6000000000 boot_strap\n```\n\n### 4.3 多集群启动示例\n\n以三个逻辑集群组成的OBASE数据库系统为例，集群号分别为1、2、3，可以分别部署在三个机房中。每个逻辑集群使用两台服务器，启动MG、TG、CG和DG共五个服务，其中MG/TG/部署于一台服务器，CG/DG部署于另一台服务器，指定主MG为182.119.80.62:8000。\n\n以admin用户登录所有服务器，在安装目录/home/admin/obase下执行启动命令。\n集群1：\nServer1\n```\nbin/manEng -r 182.119.80.62:8000 -R 182.119.80.62:8000 -i bond0 -U 3 -u 3 -C 1\nbin/transEng -r 182.119.80.62:8000 -p 8001 -m 8002 -i bond0 -C 1\n```\nServer2\n```\nbin/datEng -r 182.119.80.62:8000 -p 8005 -n obhdfs -i bond0 -C 1\nbin/calEng -r 182.119.80.62:8000 -p 8003 -z 8004 -i bond0 -C 1\n```\n\n集群2：\nServer1\n```\nbin/manEng -r 182.119.80.63:8000 -R 182.119.80.62:8000 -i bond0 -C 2\nbin/transEng -r 182.119.80.62:8000 -p 8001 -m 8002 -i bond0 -C 2\n```\nServer2\n```\nbin/datEng -r 182.119.80.62:8000 -p 8005 -n obhdfs -i bond0 -C 2\nbin/calEng -r 182.119.80.62:8000 -p 8003 -z 8004 -i bond0 -C 2\n```\n集群3：\nServer1\n```\nbin/manEng -r 182.119.80.64:8000 -R 182.119.80.62:8000 -i bond0 -C 3\nbin/transEng -r 182.119.80.62:8000 -p 8001 -m 8002 -i bond0 -C 3\n```\nServer2\n```\nbin/datEng -r 182.119.80.62:8000 -p 8005 -n obhdfs -i bond0 -C 3\nbin/calEng -r 182.119.80.62:8000 -p 8003 -z 8004 -i bond0 -C 3\n```\n在所有服务器上执行完毕命令后，在确认所有服务进程都已启动，在任意一台服务的安装目录下执行以下命令进行数据库系统的初始化：\n```\nbin/mg_admin -r 182.119.80.62 -p 8000 -t 6000000000 boot_strap\n```\n\n### 4.4 使用MySQL客户端\n\n在数据库系统初始化完成后，可以执行以下命令连接OBASE数据库：\n```\nmysql -h192.168.1.7 -P2880 -uadmin -padmin\n```\n\n参数含义：\n-h：CG所在服务器的IP地址；\n-P：启动CG服务命令\n-z：参数所指定的MySQL协议服务端口；\n-u：OBASE数据库用户名，admin为预置超级用户；\n-p：OBASE数据库用户的密码，admin用户的缺省密码为admin"