      img: require('../../../../assets/images/home/solution-banner-bg3.png'),
      descr: '某国有大型银行贷记卡预授权系统，原有系统运行在大机上，最大TPS只有1600，无法应对双十一等业 务高峰期;使用OBASE数据库后TPS突破15000，2017年双十一TPS峰值达到35000;节省大机MIPS 资源，节约成本。'
