export default "## 1. 基本概念介绍\n\n> 数据库（Database）\n\n数据库是按数据结构来组织、存储和管理数据的仓库。数据库下包括若干表、索引，以及数据库对象的元数据信息。\n\n> 表（Table）\n\n最基本的数据库对象，OBASE的表都是关系表。每个表由若干行记录组成，每一行有相同的预先定义的列。用户通过SQL语句对表进行增、删、查、改等操作。通常，表的若干列会组成一个主键，主键在整个表的数据集合内唯一。\n\n> 分片（Tablet）\n\nOBASE会把一个Table按照主键范围切分成若干个相邻的Tablet，并将 Tablet 分配到不同的 DG上存储。Tablet是数据库内部负载均衡和复制迁移的基本单位，Tablet的默认大小是64M，默认存储3个副本，多个副本均衡的分布在不同DG上。\n\n> 计算引擎（CG）\n\nOBASE数据库集群的入口，使用MySQL通信协议，所有的SQL都发到CG，由CG负责SQL解析，生成执行计划，分发执行计划，并返回执行结果。\n\n> 存储引擎（DG）\n\n存储和管理Tablet，提供数据查询服务。\n\n> 事务引擎（TG）\n\n基于LSM-Tree架构的内存事务处理引擎，负责事务相关操作，如锁管理、redo log等，保障事务的ACID特性，基于多版本并发控制协议（MVCC）实现Read Committed事务隔离级别。\n\n> 管理引擎（MG）\n\n管理集群内所有的节点，通过心跳感知节点的上下线；维护Tablet的分布信息，并管理Tablet的副本数量、复制、迁移和合并。\n\n## 2. MySQL客户端连接OBASE\n\n> 前提条件\n\n已安装OBASE数据库并拥有账户/密码：\n假设CG的IP地址为“192.168.1.7”，端口号为“2880”，用户名为“admin”，密码为“admin”。\n\n> 命令行登陆\nmysql -h192.168.1.7 -P2880 -uadmin -padmin\n\n> 参数说明\n-h：CG所在服务器的IP地址；\n-P：启动CG服务命令-z参数所指定的MySQL协议服务端口；\n-u：OBASE数据库用户名，admin为预置超级用户；\n-p：OBASE数据库用户的密码，admin用户的缺省密码为admin\n\n## 3. JDBC访问OBASE\n\n在应用服务器外使用JDBC时，DriverManager类将用于管理连接的建立。\n\n需要告诉DriverManager应与哪个JDBC驱动建立连接。完成该任务的最简单方法是：在实施了java.sql.Driver接口的类上使用Class.forName()。对于MySQL Connector/J，该类的名称是com.mysql.jdbc.Driver。采用该方法，可使用外部配置文件来提供连接到数据库时将使用的驱动类名和驱动参数。\n\n在下面的Java代码中，介绍了在应用程序的main()方法中注MySQLConnector/J的方式：\n在DriverManager中注册了驱动后，通过调用DriverManager.getConnection()，能够获得与数据库相连的连接实例。\n\n```java\nimport java.sql.Connection;\nimport java.sql.DriverManager;\nimport java.sql.PreparedStatement;\nimport java.sql.ResultSet;\n\npublic class JDBCTest {\n\tpublic static void main(String[] args) throws Exception {\n\t\tConnection connection = null;\n\t\tPreparedStatement prepareStatement = null;\n\t\tResultSet rs = null;\n\n\t\ttry {\n\t\t\t// 加载驱动\n\t\t\tClass.forName(\"com.mysql.jdbc.Driver\");\n\t\t\t// 获取连接\n\t\t\tString url = \"jdbc:mysql://192.168.100.202:2880/testdb\";\n\t\t\tString user = \"admin\";\n\t\t\tString password = \"admin\";\n\t\t\tconnection = DriverManager.getConnection(url, user, password);\n\t\t\t// 获取statement，preparedStatement\n\t\t\tString sql = \"select * from users where id=?\";\n\t\t\tprepareStatement = connection.prepareStatement(sql);\n\t\t\t// 设置参数\n\t\t\tprepareStatement.setLong(1, 1l);\n\t\t\t// 执行查询\n\t\t\trs = prepareStatement.executeQuery();\n\t\t\t// 处理结果集\n\t\t\twhile (rs.next()) {\n\t\t\t\tSystem.out.println(rs.getString(\"userName\"));\n\t\t\t\tSystem.out.println(rs.getString(\"name\"));\n\t\t\t\tSystem.out.println(rs.getInt(\"age\"));\n\t\t\t\tSystem.out.println(rs.getDate(\"birthday\"));\n\t\t\t}\n\t\t} finally {\n\t\t\t// 关闭连接，释放资源\n\t\t\tif (rs != null) {\n\t\t\t\trs.close();\n\t\t\t}\n\t\t\tif (prepareStatement != null) {\n\t\t\t\tprepareStatement.close();\n\t\t\t}\n\t\t\tif (connection != null) {\n\t\t\t\tconnection.close();\n\t\t\t}\n\t\t}\n\t}\n}\n```\n\n"