import React, { Component } from 'react'
import './block.scss'

export default class Block extends Component {
	render() {
		return (
			<div className="block-introduce">
				{
					this.props.blockList.map((item, index) => {
						return (
							<div className="container" key={index}>
								{
									item.map((blockItem, index) => {
										return (
											<div className={blockItem.class} key={index}>
												<div className="block-box" style={{ background: `#ccc url(${blockItem.background}) no-repeat center `, backgroundSize: 'cover' }}>
													<p className="title">{blockItem.title}</p>
													<p className="content">{blockItem.content}</p>
												</div>
											</div>
										)
									})
								}
							</div>
						)
					})
				}
			</div>
		)
	}
}