export default "> OBASE 1.X 版本功能包括：\n-\t数据自动分片\n-\t计算—存储分离\n-\t主从HA架构\n-\t多版本并发控制（MVCC）及事务支持（ACID）\n-\t弹性扩展机制，支持CG/DG节点横向扩展\n-\t基础数据库对象，包括数据库、表、索引、视图等\n-\t多级权限管理\n-\t导入导出、备份恢复\n\n> OBASE 2.X 版本新增的功能有：\n-\t实现Paxos协议，RTO<10s\n-\t支持跨TG节点的分布式事务，TG节点横向扩展\n-\tbloomfilter join、semi join、hash join\n-\t查询优化器\n-\tDecimal、Date、Time等数据类型\n-\tSequence序列\n-\t自增主键\n-\tDefault值\n-\t执行计划缓存\n\n> OBASE 3.X 版本新增的功能有：\n-\t存储过程\n-\t游标\n-\t触发器\n-\t唯一索引\n-\tCheck约束\n-\t大对象\n\n"