import React, { Component } from 'react'
import MarkdownIt from 'markdown-it'
import './document.scss'

const md = new MarkdownIt({ html: true })
const requireImgContext = require.context('./images', false, /.png$/)
const requireDocContext = require.context('./docs', false, /.js$/)
const images = requireImgContext.keys().map(requireImgContext)
const docs = requireDocContext.keys().map(key => requireDocContext(key).default)

const imagesMap = {}
images.forEach(img => {
  img.replace(/\/static\/media\/([^.]+)/g, (_, $1) => {
    imagesMap[`../images/${$1}.png`] = img
  })
})

function handleMDStr(str) {
  let one = str
  Object.keys(imagesMap).forEach(key => {
    one = one.replace(key, imagesMap[key])
  })
  return md.render(one)
}

export default class Document extends Component {
  constructor() {
    super()
    this.state = {
      active: 0
    }
    this.docs = docs.map(doc => handleMDStr(doc))
  }

  componentDidMount() {
    this.headerNode = document.querySelector('.header')
    this.headerNode.style.backgroundColor = '#061c4a'
  }

  componentWillUnmount() {
    this.headerNode.style.backgroundColor = ''
  }

  handleClick(index) {
    this.setState({ active: index })
  }

  render() {
    const navs = [
      '产品简介',
      '快速入门',
      '基本操作',
      '系统架构',
      '安装环境',
      '参考手册',
      '版本信息'
    ]
    const { active } = this.state
    return (
      <div className="document">
        <div className="container">
          <div className="side">
            <div className="title">文档中心</div>
            <div className="navs">
              {navs.map((nav, index) => (
                <div
                  key={index}
                  className={active === index ? 'active nav' : 'nav'}
                  onClick={this.handleClick.bind(this, index)}
                >
                  {nav}
                </div>
              ))}
            </div>
          </div>
          <div className="main">
            <div className="inner">
              <div className="title">{navs[active]}</div>
              {active === 5 ? (
                <div className="content">
                  {['OBASE分布式数据库系统安装手册', 'OBASE分布式数据库SQL使用手册'].map((e,i) => (
                    <div className="pdf-item" key={i}>
                      <span className="icon"></span>
                      <a
                        type="file"
                        href={`${process.env.PUBLIC_URL}/pdf/${e}.pdf`}
                        className="link"
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        {e}
                      </a>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="content" dangerouslySetInnerHTML={{ __html: this.docs[active] }} />
              )}
            </div>
          </div>
        </div>
      </div>
    )
  }
}
