import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import ReactSwipe from 'react-swipe'
import './carousel.scss'

export default class Carousel extends Component {
  constructor(props) {
    super()
    this.reactSwipeEl = null
    this.swipeOptions = {
      continuous: false,
      auto: 3000,
      transitionEnd: this.handleTransitionEnd.bind(this)
    }
    this.items = [{
      title: '金融级分布式关系型数据库',
      descr: 'OBASE 是一款用于管理大规模结构化数据的分布式数据库产品\r能够提供与传统关系型数据库类似的数据管理功能',
      image: require('../../assets/images/home/banner-bg1.png')
    }]
    this.state = {
      currentIndex: 0
    }
  }

  get indicators() {
    return this.items.map((_, i) => i)
  }

  handleTransitionEnd(index) {
    this.setState({
      currentIndex: index
    })
  }

  render() {
    return (
      <div className="carousel-wrapper">
        <ReactSwipe
          className="carousel items"
          swipeOptions={this.swipeOptions}
          ref={el => (this.reactSwipeEl = el)}
        >
          {this.items.map((item,index) =>
            <div className="item" key={index}>
              <div className="container">
                <div className="info">
                  <div className="title">{item.title}</div>
                  {item.descr.split('\r').map((e,i) => <div className="descr" key={i}>{e}</div>)}
                  <Link to="/about" className="more">了解更多</Link>
                </div>
                <div className="banner-img">
                  <img src={item.image} alt=""/>
                </div>
              </div>
            </div>)}
        </ReactSwipe>
        {/* <div className="indicator items">
          {this.indicators.map(e => <span className={this.state.currentIndex === e ? 'active item': 'item'} key={e}></span>)}
        </div> */}
      </div>
    )
  }
}