import React, { Component } from 'react'
import './index.scss'

export default class Card extends Component {
  render() {
    const { title, descr, img, width, direction = 'ltr', cls } = this.props.attr
    return (
      <div className={`component-card ${cls}`}>
        <div className="container">
          <div className="title">{title}</div>
          <div className={`content ${direction}`}>
            <div className="item descr-item">
              <div className="descr">{descr}</div>
            </div>
            <div className="item img-item">
              <img src={img} width={width/2} alt=""/>
            </div>
          </div>
        </div>
      </div>
    )
  }
}