import React, { Component } from 'react'
import './example.scss'

export default class Solution extends Component {
  constructor(props) {
    super(props)
    this.state = {
      current: 0
    }
  }
  get currItem() {
      return this.props.tapDetail[this.state.current]
  }
  handleTap(index) {
    this.setState(() => ({
      current: index
    }))
  }
  render() {
    return (
      <div className="example">
        <h3 className="exp-title">优秀案例</h3>
        {this.props.exampleTap.map((expItem, index) => {
          return (
            <div
              className={this.state.current === index ? 'exp-cell active' : 'exp-cell'}
              key={index}
              onClick={this.handleTap.bind(this, index)}
            >
              <i
                className="icon-cell"
                style={{
                  background: `url(${
                    this.state.current === index ? expItem.activeBackground : expItem.background
                  }) no-repeat center / contain`,
                }}
              ></i>
              <p className="title">{expItem.title}</p>
            </div>
          )
        })}
        <div className="container">
          <i
            className="exp-img"
            style={{
              background: `#F5F5F5 url(${this.currItem.background}) no-repeat center / contain`,
            }}
          ></i>
          <div className="exp-content">
            {this.currItem.rightList.map((item, index) => {
              return (
                <div key={index} className="title-cont">
                  <h3 className="title">{item.title}</h3>
                  <p className="content">{item.content}</p>
                </div>
              )
            })}
            {this.currItem.pointList.map((item, index) => {
              return (
                <div key={index} className="point-cont">
                  <p className="content">{item.content}</p>
                </div>
              )
            })}
          </div>
        </div>
      </div>
    )
  }
}
